import React, { Component } from 'react';
import { ConfigVarContext } from './../context/ConfigVarContext';


class Header extends Component {

  static contextType = ConfigVarContext;

  constructor(props) {
       super(props);
       this.state = {apiresponse : []}

  }

  _header1 =  (dataArray1) => {
      return this.context[dataArray1];
  };


 render() {
    const fav = document.getElementById("favicon");
    fav.href = this._header1("favicon");


   return (
     <div>
       <div dangerouslySetInnerHTML={{__html: this._header1('_header_')}} />

       <div class="clearfix"></div>
       <div class="header-nav" id="header">
       <div class="hederLeft" dangerouslySetInnerHTML={{__html: this._header1('logo')}} />

       <div class="rightSideNav simpleNavbar" >
             <ul class="ulmenu openMobileIs" dangerouslySetInnerHTML={{__html: this._header1('main')}} />


             <div class="container_menu_icon" onclick="menu_show_is(this,'.openMobileIs')">
                 <div class="bar1"></div>
                 <div class="bar2"></div>
                 <div class="bar3"></div>
             </div>

             <div class="menuIcons">
                 <a href="#" class="searchIcon"><i class="fa fa-search"></i> </a>
                 <a href="/cart/get"><i class="fa fa-shopping-basket"></i> </a>
                 <div class="searchTop">
                    <div dangerouslySetInnerHTML={{__html: this._header1('search')}} />
                 </div>
             </div>
       </div>
     </div>
     <div class="clearfix"></div>
   </div>
   );
 }

}

export default Header;